"use client"
import Button from '@/ui/Button';
import React, { useState } from 'react';
// import Map from './Map'; // Assume you have a Map component for displaying bridge locations
// import BridgeList from './BridgeList'; // Assume you have a List component to display bridge details

const BridgeDashboard = () => {
  const [filterParams, setFilterParams] = useState({
    stateCode: '',
    yearBuilt: '',
    functionalClass: '',
    owner: '',
  });

  const [filteredBridges, setFilteredBridges] = useState([]);

  const handleFilterChange = (e) => {
    const { name, value } = e.target;
    setFilterParams((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  const handleApplyFilters = async () => {
    // Fetch filtered data from your API endpoint (assuming it's built)
    const res = await fetch('/api/bridges', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(filterParams),
    });
    const data = await res.json();
    setFilteredBridges(data);
  };

  return (
    <div className="container mx-auto px-4 py-8">
      <h1 className="text-3xl font-bold mb-6">Bridge Dashboard</h1>

      <div className="bg-white p-6 rounded-lg shadow-md mb-6">
        <h2 className="text-xl font-semibold mb-4">Filters</h2>
        <div className="grid grid-cols-2 gap-4">
          <div>
            <label className="block text-sm font-medium mb-2">State Code</label>
            <input
              type="number"
              name="stateCode"
              value={filterParams.stateCode}
              onChange={handleFilterChange}
              className="w-full px-4 py-2 border rounded-lg"
              placeholder="Enter State Code"
            />
          </div>
          <div>
            <label className="block text-sm font-medium mb-2">Year Built</label>
            <input
              type="number"
              name="yearBuilt"
              value={filterParams.yearBuilt}
              onChange={handleFilterChange}
              className="w-full px-4 py-2 border rounded-lg"
              placeholder="Enter Year Built"
            />
          </div>
          <div>
            <label className="block text-sm font-medium mb-2">Functional Class</label>
            <input
              type="number"
              name="functionalClass"
              value={filterParams.functionalClass}
              onChange={handleFilterChange}
              className="w-full px-4 py-2 border rounded-lg"
              placeholder="Enter Functional Class"
            />
          </div>
          <div>
            <label className="block text-sm font-medium mb-2">Owner</label>
            <input
              type="number"
              name="owner"
              value={filterParams.owner}
              onChange={handleFilterChange}
              className="w-full px-4 py-2 border rounded-lg"
              placeholder="Enter Owner Code"
            />
          </div>
        </div>
        <div className="mt-6">
          <Button
            onClick={handleApplyFilters}
          
          >
            Apply Filters
          </Button>
        </div>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
        {/* <BridgeList bridges={filteredBridges} />
        <Map bridges={filteredBridges} /> */}
      </div>
    </div>
  );
};

export default BridgeDashboard;